.children-tab .img-box .img-wrapper .img_ {
  transform: scaleX(-1); }

.children-tab .safe-box .stage:nth-child(3) {
  margin-right: 50px; }

.children-tab .safe-box .stage .connecters svg {
  right: auto;
  left: 10%;
  transform: scaleX(-1); }

.children-tab .safe-box .stage .connecters {
  top: 83%; }

.children-tab .safe-box .stage:nth-child(2) .connecters {
  left: 100%;
  right: auto; }

.children-tab .safe-box .stage:nth-child(2) .connecters svg {
  transform: rotate(-37deg) translateY(0) translateX(0);
  left: 0;
  right: auto; }

.children-tab .safe-box .stage .no_ {
  transform: rotate(0deg) scale(1) translateX(0) translateY(0);
  left: 0;
  right: auto; }

.is-loaded .r-tabs-state-active .children-tab .safe-box .stage .no_ {
  transform: rotate(0deg) scale(1) translateX(0%) translateY(-1vw); }

.children-tab .img-box .img-wrapper .img_:after {
  right: 0;
  left: auto; }
